<template>
    <div>
        <el-row :gutter="10">
            <template v-for="(keyValList, groupName, i) in groupStrategy">
                <el-col :span="22" :offset="1" :key="i">
                    <el-card class="box-card" style="margin-top:5px;">
                        <div slot="header">
                            <strong>{{groupName}}</strong>
                            <el-button
                                type="text"
                                @click="clkShowLog(groupName)"
                                style="float: right; padding: 3px 0"
                            >日志</el-button>
                            <el-button
                                type="text"
                                @click="editSection(groupName)"
                                v-if="edit"
                                style="float: right; padding: 3px 4px;"
                            >修改</el-button>
                        </div>
                        <el-row>
                            <el-table :data="keyValList" stripe style="width: 100%;">
                                <el-table-column prop="strategy_key" label="key"></el-table-column>
                                <el-table-column prop="strategy_value" label="value"></el-table-column>
                                <el-table-column prop="strategy_type" label="类型"></el-table-column>
                                <el-table-column prop="comment" label="描述"></el-table-column>
                            </el-table>
                        </el-row>
                    </el-card>
                </el-col>
            </template>
        </el-row>

        <el-row style="margin-top:20px">
            <el-col align="center">
                <el-button
                    type="primary"
                    icon="el-icon-plus"
                    size="large"
                    @click="showCreateConfig = true"
                    v-if="edit"
                >新增</el-button>
                <el-button
                    type="info"
                    icon="el-icon-collection"
                    size="large"
                    @click="clkShowSnapshotList"
                >快照列表</el-button>
                <el-button
                    type="info"
                    icon="el-icon-notebook-2"
                    size="large"
                    @click="clkShowLog('')"
                >修改日志</el-button>
            </el-col>
        </el-row>

        <el-dialog title="新增配置分区" :visible.sync="showCreateConfig" width="70%">
            <SetStrategy :propService="service" :mode="'create'" />
        </el-dialog>

        <el-dialog :title="'【' + section +'】修改'" :visible.sync="showUpadteConfig" width="70%">
            <SetStrategy
                :propService="service"
                :mode="'update'"
                :propSection="section"
                ref="UpdateSection"
            />
        </el-dialog>

        <el-dialog title="配置快照列表" :visible.sync="showSnapshotList" width="90%">
            <el-row :gutter="20">
                <el-col :span="12">
                    <el-table :data="snapshotList" stripe style="width: 100%;" height="500px">
                        <el-table-column prop="id" label="ID"></el-table-column>
                        <el-table-column prop="create_time" label="创建时间" width="180"></el-table-column>
                        <el-table-column prop="cause_user" label="更新人"></el-table-column>
                        <el-table-column prop="cause_section" label="分区"></el-table-column>
                        <el-table-column prop="cause_section" label="快照内容">
                            <template slot-scope="scope">
                                <el-button
                                    @click="displaySnapshot(scope.row)"
                                    type="text"
                                    size="small"
                                >查看</el-button>
                            </template>
                        </el-table-column>
                    </el-table>
                    <el-pagination
                        @current-change="getSnapshotList"
                        :current-page.sync="snapshotPage"
                        :page-count="snapshotTotalPage"
                        align="center"
                        style="margin-top:10px;"
                    ></el-pagination>
                </el-col>
                <el-col :span="12">
                    <p>
                        <strong>ID：</strong>
                        {{snapshot.id}}
                    </p>
                    <p>
                        <strong>md5：</strong>
                        {{snapshot.md5}}
                    </p>
                    <p>
                        <strong>时间：</strong>
                        {{snapshot.create_time}}
                    </p>
                    <ShowJson :showStr="snapshot.snapshot"></ShowJson>
                </el-col>
            </el-row>
        </el-dialog>

        <el-dialog title="修改日志" :visible.sync="showLogList" width="50%">
            <el-row style="margin-bottom:20px;">
                <el-date-picker
                    value-format="yyyy-MM-dd"
                    style="width:400px"
                    v-model="logTime"
                    type="daterange"
                    unlink-panels
                    range-separator="至"
                    start-placeholder="开始日期"
                    end-placeholder="结束日期"
                    @change="getEditLog"
                ></el-date-picker>
            </el-row>
            <el-row>
                <el-timeline>
                    <template v-for="(item, i) in logList">
                        <el-timeline-item :timestamp="item.create_time" placement="top" :key="i">
                            <el-card>
                                <strong>
                                    <el-tag
                                        :type="item.action_tag"
                                        style="margin-right:5px;"
                                    >{{item.action_text}}</el-tag>
                                    {{item.section}}
                                </strong>
                                <el-table :data="item.detail" v-if="item.action != 'delete'">
                                    <el-table-column prop="strategy_key" label="key"></el-table-column>
                                    <el-table-column
                                        prop="strategy_value"
                                        label="value"
                                    ></el-table-column>
                                    <el-table-column prop="strategy_type" label="类型"></el-table-column>
                                </el-table>
                            </el-card>
                        </el-timeline-item>
                    </template>
                </el-timeline>
            </el-row>
        </el-dialog>
    </div>
</template>

<script>
import SetStrategy from "./SetStrategy";
import moment from "moment";

export default {
    props: ["propStrategyList"],
    data() {
        return {
            service: "",
            serviceInfo: {},
            strategyList: [],
            showCreateConfig: false,
            section: "",
            showUpadteConfig: false,
            showSnapshotList: false,
            snapshotList: [],
            snapshotTotalPage: 0,
            snapshotPage: 1,
            snapshot: {},

            showLogList: false,
            logList: [],
            logTime: "",
            log: {},
            logSection: "",
            logActionConf: {
                create: "创建",
                update: "更新",
                delete: "删除"
            },
            logActionTagConf: {
                create: "primary",
                update: "success",
                delete: "danger"
            }
        };
    },
    components: {
        SetStrategy
    },
    computed: {
        groupStrategy: function() {
            let obj = {};
            this.strategyList.forEach(item => {
                if (obj[item.section] == undefined) {
                    obj[item.section] = [];
                }
                obj[item.section].push(item);
            });
            return obj;
        },
        edit: function() {
            return (
                (this.serviceInfo.Creator == "system" &&
                    this.$store.state.user.user_type == 10) ||
                this.serviceInfo.Creator == this.$store.state.user.user_name
            );
        }
    },
    mounted() {
        this.switchService();
    },
    methods: {
        switchService() {
            if (this.$route.query.service != "") {
                this.service = this.$route.query.service;
            }
            this.getServiceConfig();
            this.getServiceInfo();
        },
        async getServiceConfig() {
            this.strategyList = [];
            let res = await this.$api.qingl.getServiceConfig(this.service, "");
            if (res.errcode == 0) {
                this.strategyList = res.data;
            } else {
                this.$message(res.errmsg);
            }
        },
        async getServiceInfo() {
            let res = await this.$api.qingl.getSingleService(this.service);
            if (res.errcode == 0) {
                this.serviceInfo = res.data;
            }
        },
        editSection(n) {
            this.section = n;
            this.showUpadteConfig = true;
            setTimeout(() => {
                this.$refs["UpdateSection"].getStrategys();
            }, 300);
        },
        async getSnapshotList() {
            let res = await this.$api.qingl.getServiceSnapshotList(
                this.service
            );
            if (res.errcode == 0) {
                this.snapshotList = res.data.list;
            } else {
                this.snapshotList = [];
            }
        },
        async getEditLog() {
            let res = await this.$api.qingl.getServiceSectionLog(
                this.service,
                this.logSection,
                this.logTime[0],
                this.logTime[1]
            );
            if (res.errcode == 0) {
                let l = [];
                res.data.forEach(item => {
                    item.detail = JSON.parse(item.detail);
                    item.action_text = this.logActionConf[item.action];
                    item.action_tag = this.logActionTagConf[item.action];
                    l.push(item);
                });
                this.logList = l.reverse();
            } else {
                this.logList = [];
            }
        },
        clkShowSnapshotList() {
            this.getSnapshotList();
            this.showSnapshotList = true;
        },
        clkShowLog(se) {
            this.logTime = [
                moment()
                    .subtract(7, "days")
                    .format("YYYY-MM-DD"),
                moment().format("YYYY-MM-DD")
            ];
            this.logSection = se;
            this.getEditLog();
            this.showLogList = true;
        },
        displaySnapshot(s) {
            this.snapshot = s;
        },
        displayLog(l) {
            this.log = l;
        }
    }
};
</script>
