import Vue from 'vue'
import Router from 'vue-router'

//概览
import ServiceDetail from '@/Control/Service/Detail'
import ServiceList from '@/Control/Service/List'

Vue.use(Router)
const router = new Router({
    routes: [
        {
            path: '/service/detail',
            name: 'ServiceDetail',
            component: ServiceDetail,
            meta: {
                title: [],
            }
        },
        {
            path: '/service/list/:service_type',
            name: 'ServiceList',
            component: ServiceList,
            meta: {
                title: [],
            }
        },
    ]
})
router.beforeEach((to, from, next) => {
    $(window).unbind('scroll');
    next();
})
router.afterEach((to, from, next) => {
    window.scrollTo(0, 0);
});
export default router;
