<template>
    <div>
        <el-row v-if="mode == 'create'">
            <el-col :span="18" :offset="1">
                <el-form :label-position="'right'" label-width="100px">
                    <el-form-item label="分区名：">
                        <el-input v-model="section"></el-input>
                    </el-form-item>
                </el-form>
            </el-col>
            <el-col :span="2" :offset="1"></el-col>
        </el-row>

        <el-row>
            <template v-for="(item, index, i) in strategys">
                <el-col :span="20" :offset="2" :key="i">
                    <el-card class="box-card" style="margin-top:5px;">
                        <div style="text-align:right;">
                            <el-button @click="deleteStrategy(index)" type="text" size="small">删除</el-button>
                        </div>
                        <el-form :label-position="'right'" label-width="70px">
                            <el-form-item label="类型：">
                                <el-radio
                                    v-model="strategys[index].strategy_type"
                                    label="string"
                                >string</el-radio>

                                <el-radio v-model="strategys[index].strategy_type" label="bool">bool</el-radio>
                                <el-radio v-model="strategys[index].strategy_type" label="int">int</el-radio>
                                 <el-radio v-model="strategys[index].strategy_type" label="float">float</el-radio>
                            </el-form-item>

                            <el-form-item label="key：">
                                <el-input
                                    v-model="strategys[index].strategy_key"
                                    placeholder="请输入子策略key，由英文字符和下划线组成"
                                ></el-input>
                            </el-form-item>

                            <el-form-item label="value：">
                                <el-input
                                    v-model="strategys[index].strategy_value"
                                    placeholder="子策略默认值"
                                    type="textarea"
                                    :rows="2"
                                ></el-input>
                            </el-form-item>

                            <el-form-item label="描述：">
                                <el-input
                                    type="textarea"
                                    v-model="strategys[index].comment"
                                    rows="2"
                                    placeholder="请输入策略描述信息"
                                ></el-input>
                            </el-form-item>
                        </el-form>
                    </el-card>
                </el-col>
            </template>
        </el-row>

        <el-row style="margin-top:30px;" v-if="mode == 'create'">
            <el-col :span="24" align="center">
                <el-button @click="addString" type="info">新增一条策略</el-button>
                <el-button @click="createSection" type="primary">提交</el-button>
            </el-col>
        </el-row>

        <el-row style="margin-top:30px;" v-if="mode == 'update'">
            <el-col :span="24" align="center">
                <el-button @click="addString" type="info">新增一条策略</el-button>
                <el-button @click="updateSection" type="primary">修改</el-button>
            </el-col>
        </el-row>
    </div>
</template>

<script>
export default {
    props: ["propService", "propSection", "mode"],
    data() {
        return {
            strategys: [],
            section: "",
            deleteStrategys: []
        };
    },
    methods: {
        addString: function() {
            this.strategys.push({
                strategy_key: "",
                strategy_value: "",
                comment: "",
                strategy_type: "string"
            });
        },
        deleteStrategy(index) {
            if (this.mode == "create") {
                this.strategys = this.strategys.filter((tab, i) => i !== index);
            }

            if (this.mode == "update") {
                if (
                    this.strategys[index].id != undefined &&
                    this.strategys[index].id > 0
                ) {
                    this.deleteStrategys.push(this.strategys[index]);
                }
                this.strategys = this.strategys.filter((tab, i) => i !== index);
            }
        },
        async createSection() {
            let res = await this.$api.qingl.createSection(
                this.propService,
                this.section,
                this.strategys
            );
            if (res.errcode == 0) {
                this.$message("创建成功，已生效");
                this.$tool.reload();
            } else {
                this.$message(res.errmsg);
            }
        },
        async updateSection() {
            let res = await this.$api.qingl.updateSection(
                this.propService,
                this.propSection,
                this.strategys,
                this.deleteStrategys
            );
            if (res.errcode == 0) {
                this.$message("修改成功，已生效");
                this.$tool.reload();
            } else {
                this.$message(res.errmsg);
            }
        },
        async getStrategys() {
            this.deleteStrategys = [];
            if (
                this.propService == null ||
                this.propSection == null ||
                this.propService == "" ||
                this.propSection == ""
            ) {
                return;
            }

            if (this.mode == "create") {
                return;
            }

            let res = await this.$api.qingl.getServiceConfig(
                this.propService,
                this.propSection
            );
            if (res.errcode == 0) {
                this.strategys = res.data;
            }
        }
    }
};
</script>
