<template>
    <div id="app">
        <el-container>
            <el-header :height="headerHeight">
                <myheader
                    :userName="userName"
                    :userType="userType"
                    contentType="control"
                    @setHeaderHeight="setHeaderHeight"
                ></myheader>
            </el-header>
            <el-container>
                <el-aside :width="showSlideMenu?open:close">
                    <slidemenu
                        :currentMenuData="insightMenuList"
                        :showSlideMenu="showSlideMenu"
                        @expand="expand"
                        style="overflow-y:auto;ovreflow-x:hidden"
                    ></slidemenu>
                </el-aside>
                <el-main style="margin-bottom: 160px;">
                    <el-row>
                        <el-col :span="21">
                            <el-breadcrumb separator-class="el-icon-arrow-right">
                                <el-breadcrumb-item :to="{ path: '/insights/index' }">首页</el-breadcrumb-item>
                                <el-breadcrumb-item
                                    v-for="(item, idx) in $route.meta.title"
                                    :key="idx"
                                >{{item}}</el-breadcrumb-item>
                            </el-breadcrumb>
                        </el-col>
                    </el-row>
                    <el-divider></el-divider>
                    <router-view @hook:mounted="handleChildCreated" />
                </el-main>
            </el-container>
        </el-container>
    </div>
</template>
<script>
import myheader from "@/components/EleHeader";
import slideMenu from "@/components/SlideMenu";

import "@/assets/css/common.less";
import Menu from "./menu";
import moment from "moment";

export default {
    name: "Insights",
    data() {
        return {
            showSlideMenu: true,
            currentMenuData: [],
            userType: 0,
            userName: "",
            contentType: "insights",
            insightMenuList: [],
            allPages: [],
            isInitAllPage: false,
            close: "64px",
            open: "210px",
            headerHeight: "60px",
            updateDailog: false
        };
    },
    components: {
        myheader: myheader,
        slidemenu: slideMenu
    },
    methods: {
        expand(flag) {
            this.$set(this, "showSlideMenu", flag);
        },
        setHeaderHeight(h) {
            this.headerHeight = h;
        },
        async getUserInfo() {
            let res = await this.$api.user.getUserInfoByGo();
            if (res.errcode == 0) {
                this.userType = res.data.user_type;
                this.userName = res.data.user_name;
            }
            this.$store.commit("setUsername", this.userName);
            this.$store.commit("setUserType", this.userType);

            this.getInsightMenu(res.data);
            this.handleChildCreated();
        },
        getInsightMenu: function(insightUser) {
            this.insightMenuList = Menu;
        },
        handleChildCreated() {
            if (this.isInitAllPage) {
                this.checkUpdatePage();
            }
        },
        checkUpdatePage: function() {
            let initPageAt = this.PageInitAt;

            if (moment(initPageAt).isBefore(moment().subtract(3600, "s"))) {
                window.location.reload();
            }
        },
    },
    mounted() {
        this.getUserInfo(false);
    }
};
</script>
