<template>
    <div>
        <el-row>
            <el-table :data="serviceList" stripe style="width: 100%;">
                <el-table-column prop="id" label="ID"></el-table-column>
                <el-table-column prop="service" label="服务名">
                    <template slot-scope="scope">
                        <router-link
                            :to="{ path: '/service/detail', query: { service: scope.row.service}}"
                        >{{scope.row.service}}</router-link>
                    </template>
                </el-table-column>
                <el-table-column prop="comment" label="描述"></el-table-column>
                <el-table-column prop="creator" label="创建人"></el-table-column>
                <el-table-column prop="create_time" label="创建时间"></el-table-column>
            </el-table>
        </el-row>

        <el-row
            style="margin-top:20px"
            v-if="serviceType == 'mine' || (serviceType == 'system' && $store.state.user.user_type == 10)"
        >
            <el-col align="center">
                <el-button
                    type="primary"
                    icon="el-icon-plus"
                    size="large"
                    @click="showCreate = true"
                >新增</el-button>
            </el-col>
        </el-row>

        <el-dialog title="新增服务配置" :visible.sync="showCreate" width="50%">
            <el-row>
                <el-col :span="18" :offset="1">
                    <el-form :label-position="'right'" label-width="100px">
                        <el-form-item label="服务名：">
                            <el-input v-model="service.service"></el-input>
                        </el-form-item>
                        <el-form-item label="描述：">
                            <el-input v-model="service.comment"></el-input>
                        </el-form-item>
                    </el-form>
                </el-col>
            </el-row>
            <el-row style="margin-top:30px;">
                <el-col :span="24" align="center">
                    <el-button @click="createService" type="primary">创建</el-button>
                </el-col>
            </el-row>
        </el-dialog>
    </div>
</template>

<script>
export default {
    data() {
        return {
            serviceType: "system",
            serviceList: [],
            showCreate: false,
            service: {
                service: "",
                comment: ""
            }
        };
    },
    mounted() {
        this.switchService();
    },
    watch: {
        $route: {
            deep: true,
            handler: function() {
                this.switchService();
            }
        }
    },
    methods: {
        switchService() {
            if (this.$route.params.service_type != "") {
                this.serviceType = this.$route.params.service_type;
            } else {
                this.serviceType = this.$route.params.service_type;
            }
            this.getService();
        },
        async getService() {
            let res = await this.$api.qingl.getServiceList(this.serviceType);
            if (res.errcode != 0) {
                return;
            }
            this.serviceList = res.data;
        },
        async createService() {
            this.service.service_type = this.serviceType
            let res = await this.$api.qingl.createService(this.service)
            if (res.errcode == 0) {
                this.$message("创建成功，已生效");
                this.$tool.reload();
            } else {
                this.$message(res.errmsg);
            }
        }
    }
};
</script>
